import React, { useState } from 'react';
require('../styles/NavBar.css');

const navLogo = require('../images/current-logo-indigo-2.svg');
const mrBeastSurprised = require('../images/mr-beast-surprised.png');

const NavBar = () => {
  const [ isHamburgerOpen, setIsHamBurgerOpen ] = useState(false);
  return (
    <nav className={`navBar ${isHamburgerOpen && 'transitionNavBarBackground'}`}>
      <a href="https://current.com/">
        <span role="button" tabIndex="0">
          <img className="navLogo" alt="Current Logo" src={navLogo} />
        </span>
      </a>
      <div className="navMenu">
        <a 
          href="https://current.com/faster-direct-deposit" 
          className="navLink"
          target="_blank"
        >
          Benefits
        </a>
        <a 
          href="https://current.com/blog" 
          className="navLink"
          target="_blank"
        >
          Community
        </a>
        <a 
          href="https://current.com/about-us" 
          className="navLink"
          target="_blank"
        >
          About Us
        </a>
      </div>
      <button className="navSignUp">Sign Up</button>
      <div 
        className={`navHamburgerMenu ${isHamburgerOpen && 'hamburgerOpen'}`} 
        onClick={() => setIsHamBurgerOpen(!isHamburgerOpen)}
      >
        <span className="navHamBurger"></span>
      </div>
      {isHamburgerOpen && 
        <aside className="navBarSideDrawerOpen">
          <img 
            src={mrBeastSurprised} 
            alt="Mr Beast Surprised" 
            className="mrBeastSurprised"/>
        </aside>
      }
    </nav>
  );
};

export default NavBar