import React from 'react';
require('../styles/VerifyModal.css');
const mrBeastCircle = require('../images/beast-circle-text-d9b6f5e9191ab2358f498f8b25dcfe69.svg');


const VerifyModal = ({ formValues, setIsVerifyModalOpen }) => {
  return (
    <section 
      className="verifyModalContainerBackground"
      onClick={() => setIsVerifyModalOpen(false)}
    >
      <div className="verifyModalContainer">

        <h2 className="verifyModalTitle">Does this information look correct?</h2>
        <p><span>Full Name: </span>{formValues.fullName}</p>
        <p><span>Email: </span>{formValues.email}</p>
        {
          formValues.optionalMessage && 
          <p><span>Message: </span>{formValues.optionalMessage}</p>
        }
        <p><span>Product Type: </span>{formValues.productType}</p>
        <button
          className="verifyModalGoBack"
          onClick={() => setIsVerifyModalOpen(false)}
        >Nope, go back</button>
        <button 
          className="verifyModalContinue"
          onClick={() => setIsVerifyModalOpen(false)}
        >Yes, lets go!</button>
        <img 
          src={mrBeastCircle} 
          alt="Mr Beast Cicle" 
          className="mrBeastCircle"
        />
      </div>

    </section>
  )
}

export default VerifyModal